body{background-color:#E2D4B7;margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

.receiptContainer{background-color:#433A3F;color:white;width:500px;max-width:500px;padding:2px;margin-left:auto;margin-right:auto;margin-bottom:15px;border-radius:6px;display:block;min-height:175px}.actions{float:right;font-size:1.5rem;max-width:100px;margin-top:0.2em;margin-right:5px}.actions a:link,a:visited,a:hover,a:active{color:white}.actions span{margin:0px 4px;width:5px}.actions span:hover{cursor:pointer}.icon{width:1em}#save{color:#04c911}#delete{color:#d60c0c}.receiptDetails{text-align:left;padding-left:15px}.receiptDetails ul{list-style-type:none;padding-left:0}h1{display:inline}

.App{margin-top:10px;text-align:center}.App-logo{height:40vmin;pointer-events:none}@media (prefers-reduced-motion: no-preference){.App-logo{-webkit-animation:App-logo-spin infinite 20s linear;animation:App-logo-spin infinite 20s linear}}.App-header{background-color:#282c34;min-height:100vh;display:flex;flex-direction:column;align-items:center;justify-content:center;font-size:calc(10px + 2vmin);color:white}.App-link{color:#61dafb}@-webkit-keyframes App-logo-spin{from{transform:rotate(0deg)}to{transform:rotate(360deg)}}@keyframes App-logo-spin{from{transform:rotate(0deg)}to{transform:rotate(360deg)}}.navButton{background-color:#433A3F;color:white;border:2px solid #433A3F;font-size:1em;padding:0.5em;border-radius:5px;margin:0 3px}.navButton:hover{border:2px white solid}.navButton:focus{outline:none;box-shadow:none}nav{margin:15px}

